import imgIncrement from './../../img/icons/increment.svg'
import imgDecrement from './../../img/icons/decrement.svg'

const Count = ({count, increment, id, decrement}) =>{
return (
        <>    
          <img className='basket__product__count__img__decrement button' onClick={()=>{decrement(id)}} src={imgDecrement} alt=''></img>
            <div className='basket__product__count__result'>{count}</div>
          <img className='basket__product__count__img__increment button' onClick={()=>{increment(id)}} src={imgIncrement} alt=''></img> {/*   .,       */}
        </>
        )
}

export default Count;

